#container {
  width: 1280px;
  height: 720px;
}

.manual {
  width: 1280px;
  height: 720px;
  margin: auto;
  display: flex;
}
.page {
  width: 640px;
  height: 720px;
  display: flex;
  flex-direction: column;
}

.messageBox {
  margin-top: 270px;
  margin-left: 15px;
  width: 447px; /* 596 */
  height: 120px; /* 160 */
  background-size: cover;
  display: flex;
  justify-content: center;
}

.messageBoxButton {
  width: 108px; /*144*/
  height: 25px; /*33*/
  background-size: cover;
  margin-top: 80px;
}

.languageSelector {
  margin-top: 180px;
  margin-left: 70px;
  width: 360px;
  height: 32px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.languageLogo {
  width: 32px;
  height: 32px;
  background-size: cover;
}
.leftPage {
  flex-direction: column;
  padding-left: 151px;
  padding-top: 105px;
  width: 476px;
}

.rightPage {
  padding-top: 434px;
  padding-left: 170px;
}

.pagination {
  height: 24px;
  display: flex;
  flex-direction: row;
  width: 435px;
}

.pageNumber {
  width: 24px;
  height: 24px;
  margin-right: 10px;
  background-size: cover;
}
.spellList {
  width: 432px;
  height: 384px;
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
}
.spell {
  width: 108px;
  height: 96px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  color: #FFF;
}

.spellIcon {
  width: 59px;
  height: 59px;
  background-size: cover;
}
.section {
  height: 22px;
  display: flex;

}

.sectionIcon {
  height: 22px;
  width: 22px;
  margin-right: 15px;
  background-size: cover;
}
html {
  width: 100%;
  height: 100%;
  min-width: 1280px;
  min-height: 720px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #211f21;
}

body {
  margin: 0;
  padding: 0;
}
